
import React from 'react';
import './NavBar.css';
import logo from './strayfarm.png';
import menu from './menu.png';
import { BrowserRouter as Router,Routes, Route, Link } from 'react-router-dom';

const NavBar = ({openNav,closeNav}) => {
    openNav=()=>{
      var width = window.innerWidth;
      if(width <=900 && width > 800)
      {
        document.getElementById("myNav").style.width = "35%";
      }
      else if(width <=800 && width > 700)
      {
        document.getElementById("myNav").style.width = "40%";
      }
      else if(width <=700 && width > 600)
      {
        document.getElementById("myNav").style.width = "45%";
      }
      else if(width <=600 && width >500)
      {
        document.getElementById("myNav").style.width = "50%";
      }
      else if(width <=500 && width> 400)
      {
        document.getElementById("myNav").style.width = "62%";
      }
      else if(width <=400 && width > 200)
      {
        document.getElementById("myNav").style.width = "75%";
      }
      else if(width <=200 && width > 0)
      {
        document.getElementById("myNav").style.width = "100%";
      }
    }
    closeNav=()=>{
      document.getElementById("myNav").style.width = "0%";
    }


  return (
    <div className='myNav'>
        <div className="nav">
              <div className='div1'>
                  <Link to="/strayfarm/">
                    <img className='l' src={logo} alt="Logo" /> 
                  </Link>
                  <Link to="/strayfarm/" style={{ textDecoration: 'none' }}><h2 className='s' >STRAY FARM</h2> </Link>
              </div>
              <div className='div2'>
                <Link style={{ textDecoration: 'none' }} to="/strayfarm/gallery"><h2 className='n' >Our Gallery</h2> </Link>
                <Link style={{ textDecoration: 'none' }} to="/strayfarm/adoption"><h2 className='n' >Adoption</h2> </Link>
                <Link style={{ textDecoration: 'none' }} to="/strayfarm/donate"><h2 className='n' >Donate</h2> </Link>
                <Link style={{ textDecoration: 'none' }} to="/strayfarm/contact"><h2 className='n' >Contact us</h2> </Link>
                <Link style={{ textDecoration: 'none' }} to="/strayfarm/about"><h2 className='n' >About Us</h2> </Link>


              </div>
              <div className='div3'>
                 <img className='m' onClick={openNav} src={menu} alt="Logo" /> 
              </div>
        </div>
        <div id="myNav" class="overlay">
          <a href="javascript:void(0)" class="closebtn" onClick={closeNav}>&times;</a>
          <div class="overlay-content">
            <a onClick={closeNav}><Link to="/strayfarm/gallery">Our Gallery</Link></a>
            <a onClick={closeNav}><Link to="/strayfarm/adoption">Adoption</Link></a>
            <a onClick={closeNav}><Link to="/strayfarm/donate">Donate</Link></a>
            <a onClick={closeNav}><Link to="/strayfarm/contact">Contact Us</Link></a>
            <a onClick={closeNav}><Link to="/strayfarm/about">About Us</Link></a>
      </div>
    </div>
    </div>


        
    
  );
}

export default NavBar;