import './Home.css';
import { TwitterTweetEmbed} from 'react-twitter-embed';
import { BrowserRouter as Router,Routes, Route, Link } from 'react-router-dom';
function Home() {
  return (
    <div className="Home">
      <div className='mainH'>
        <h1 className='he'><u>Welcome</u></h1>
        <p className='hom'>
            Currently, we are working on helping stray animals in any way possible.<br /> We are in contact with some of the rescuers who rescue animals and takes care of them and then release them back safely.
            <br />We are aiming to having our own <i>clinic and shelter</i> so we can serve animals the love they deserve.<br/><br/>
              <b><i>Wanna help us?</i></b>  <Link style={{ textDecoration: 'none' }} to="/strayfarm/donate"><button className='hd'>Donate</button></Link>
              <br/><br/>
              <b><i>Any emergency?</i></b> <a href='tel:+917982294822'><button className='hd'>Call Us</button></a>
            <br/><br/>
            <u>Recent tweet</u> :-
            <TwitterTweetEmbed className='tweet'
              tweetId={'1617007039523491840'}
              
            />
        </p>    
      </div>
    </div>

  );
}

export default Home;